.video {
     position: relative;
     max-height: 50rem;
     overflow: hidden;

     .overlay {
          position: absolute;
          width: 100%;
          height: 100%;
          /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+58,ffffff+100&0.8+0,0.5+100 */
          background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.63) 58%, rgba(255, 255, 255, 0.5) 100%);
          background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.63) 58%, rgba(255, 255, 255, 0.5) 100%);
          background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.63) 58%, rgba(255, 255, 255, 0.5) 100%);
          filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#cc891c1e', endColorstr='#80ffffff', GradientType=1);


     }

     video {
          display: block;
          width: 100%;
     }
     .contenido-video{
          display: flex;
          height: 100%;
          align-items: center;
          color: $Cblanco;
          z-index: 1;
          h2{
               display: flex;
               flex-direction: column;
               align-items: flex-start;
               font-size: 7rem;
          }
          span{
               background-color: $Crosa;
               font-size: 2rem;
               padding: 1rem 2rem;
               font-weight: $Fregular;
          }
     }
}